import {useCallback} from 'react';

export function Notice(props) {
    const {
        className,
        children,
        duration,
        setShowNotice,
        title,
    } = props;

    const onCloseHandler = useCallback(
        () => {
            if (setShowNotice) {
                setShowNotice(false);
            }
        },
        [setShowNotice]
    );

    return (
        <div
            className = {`notice ${className}`}
        >
            <div className = {'notice-header'}>
                <button
                    onClick = {onCloseHandler}
                >
                    {'X'}
                </button>
                <h1 className = {'notice-header-title'}>{title}</h1>
            </div>
            {children}
        </div>
    );
}
